<template>
  <div class="search">
    <van-nav-bar title="商品搜索" left-arrow @click-left="$router.go(-1)" />

    <van-search v-model.trim="search" @search="goSearch(search)" show-action placeholder="请输入搜索关键词" clearable>
      <template #action>
        <div @click="goSearch(search)">搜索</div>
      </template>
    </van-search>

    <!-- 搜索历史 -->
    <div class="search-history" v-if="history.length>0">
      <div class="title">
        <span>最近搜索</span>
        <van-icon @click="clear" name="delete-o" size="16" />
      </div>
      <div class="list">
        <div v-for="item in history" :key="item" class="list-item" @click="goSearch(item)">{{ item }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { getHistoryList, setHistoryList } from '@/utils/storage'
export default {
  name: 'SearchIndex',
  data () {
    return {
      history: [],
      search: ''
    }
  },
  created () {
    this.history = getHistoryList()
  },
  methods: {
    goSearch (key) {
      // console.log(key)
      // 点击了搜索 或者 点击了历史记录 要做什么
      // 搜索的值和历史记录 被放进了形参key中 所以先检索key在不在历史记录history里
      const index = this.history.indexOf(key)
      // indexOf是检索 元素a 在不在 数组A 中,如果在,则返回它的下标,如果不在则返回-1
      // console.log(index)
      // 如果搜索一个有历史记录的值,则删除这个值 并将它重新添加到数组首部
      if (index !== -1) {
        this.history.splice(index, 1)
        this.history.unshift(key)
      } else { // 如果没有历史记录则加进数组的首部即可
        if (this.search.trim() === '') return
        this.history.unshift(key)
        this.search = ''
      }
      setHistoryList(this.history)
      // 跳转到到搜索列表页
      this.$router.push(`/searchlist?search=${key}`)
    },
    clear () {
      this.history = []
      setHistoryList([])
    }
  }
}
</script>

<style lang="less" scoped>
.search {
  .searchBtn {
    background-color: #fa2209;
    color: #fff;
  }
  ::v-deep .van-search__action {
    background-color: #c21401;
    color: #fff;
    padding: 0 20px;
    border-radius: 0 5px 5px 0;
    margin-right: 10px;
  }
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .title {
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
  }
  .list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 0 10px;
    gap: 5%;
  }
  .list-item {
    width: 30%;
    text-align: center;
    padding: 7px;
    line-height: 15px;
    border-radius: 50px;
    background: #fff;
    font-size: 13px;
    border: 1px solid #efefef;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-bottom: 10px;
  }
}

</style>
